<template>
	<div class="app-wrapper" @dragenter.prevent="showMask()">
		<div class="main">
			<div class="sidebar">
				<div class="logo"></div>
				<sidebar></sidebar>
				<div class="stroage">
					<el-progress type="circle" :percentage="25"></el-progress>
					<div class="storage_text">
						<p class="used">已用: {{storage.used}}</p>
						<p class="total">共计: {{storage.total}}</p>
					</div>
				</div>
			</div>

			<div class="app-container">
				<div class="header">
					<div class="title">{{title}}</div>
					<div class="head_right">
						<div class="search">
							<i class="el-icon-search"></i>
							<input type="text" />
						</div>

						<el-dropdown @command="handleCommand">
							<span class="el-dropdown-link">
								{{user.name}}
								<i class="el-icon-arrow-down el-icon--right"></i>
							</span>
							<el-dropdown-menu slot="dropdown">
								<el-dropdown-item icon="el-icon-receiving" command="logout">退出登录</el-dropdown-item>
							</el-dropdown-menu>
						</el-dropdown>
					</div>
				</div>

				<div class="tags-view">
					<bread-nav></bread-nav>
				</div>

				<div class="app-main">
					<router-view></router-view>
				</div>
			</div>
		</div>

		<div class="bottom-status">
			<div class="status-path">
				<i class="el-icon-folder-opened margin-right-5"></i>
				<span class="path">{{status.path}}</span>
			</div>
		</div>

		<div
			:class="{'mask-top':dragMask}"
			@drop.prevent="drop($event)"
			@dragleave.prevent="dragLeave"
			@dragover.prevent
		></div>
		<div :class="{'drag-mask':dragMask}">
			<div class="tip">在此处释放以上传到当前目录</div>
		</div>
	</div>
</template>

<script>
import { Sidebar, BreadNav } from './components'

export default {
	components: {
		Sidebar,
		BreadNav
	},
	data() {
		return {
			title: '我的文件',
			user: {
				name: 'admin'
			},
			storage: {
				used: '25GB',
				total: '100GB'
			},
			status: {
				path: 'C:Users\\Lenovo\\Desktop\\nsync'
			},
			dragMask: 0
		}
	},
	methods: {
		showMask: function() {
			if (this.$route.path == '/myfile') this.dragMask = 1
		},
		drop: function(e) {
			var files = []
			;[].forEach.call(
				e.dataTransfer.files,
				function(file) {
					files.push(file)
				},
				false
			)
			console.log(files)
			this.dragMask = 0
		},
		dragLeave: function() {
			this.dragMask = 0
		},
		handleCommand(command) {
			console.log(command)
		}
	},
	watch: {
		'$route.path': function(to, from) {
			this.title = this.$route.meta.title
			document.title = 'nsync - ' + this.$route.meta.title
		}
	}
}
</script>

<style lang="scss">
@import '@/styles/common';

$sidebar_width: 200px;
$status_height: 25px;
$logo_height: 80px;
$tags_height: 30px;
$logo_src: '../assets/nsync_png.png';

.mask-top {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	z-index: 999;
}

.drag-mask {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background-color: rgba(177, 177, 177, 0.376);
	z-index: 998;
	display: flex;
	justify-content: center;
	align-items: center;

	.tip {
		width: 90vw;
		height: 90vh;
		line-height: 90vh;
		border: 10px dashed rgb(151, 151, 151);
		font-size: 50px;
		text-align: center;
		color: rgb(151, 151, 151);
	}
}

.app-wrapper {
	width: 100%;
	height: 100%;

	.main {
		width: inherit;
		height: calc(100% - #{$status_height});
		position: absolute;

		.sidebar {
			width: $sidebar_width;
			height: 100%;
			background-color: rgb(255, 255, 255);
			position: absolute;
			box-shadow: 2px 0 15px rgb(182, 182, 182);
			z-index: 99;

			.logo {
				width: inherit;
				height: $logo_height;
				background: url($logo_src) no-repeat center center;
				background-size: 70%;
				//border: 1px solid #f00;
			}

			.stroage {
				width: inherit;
				height: 100px;
				position: absolute;
				display: flex;
				justify-content: space-between;
				padding: 10px;
				bottom: 0;
				box-sizing: border-box;
				border-top: 1px solid rgb(226, 226, 226);

				.el-progress-circle {
					height: 75px !important;
					width: 75px !important;
				}

				.storage_text p {
					margin: 10px 0;
				}
			}
		}

		.app-container {
			width: calc(100% - #{$sidebar_width});
			height: 100%;
			background-color: rgb(211, 241, 243);
			margin-left: $sidebar_width;
			position: relative;
			//overflow: hidden;

			.header {
				height: $logo_height;
				//border: 1px solid #f00;

				display: flex;
				justify-content: space-between;
				align-items: center;
				padding: 0 20px;

				.title {
					font-size: 25px;
				}

				.head_right {
					display: flex;
					justify-content: space-between;
					align-items: center;
					width: 350px;
					margin: 0 30px;

					.search {
						display: flex;
						justify-content: center;
						align-items: center;
						padding: 8px 15px;
						background-color: #fff;
						border-radius: 100px;
						color: rgb(195, 195, 195);

						input {
							outline: none;
							background-color: transparent;
							border: none;
							margin-left: 10px;
						}
					}

					.el-dropdown-link {
						cursor: pointer;
						color: #202020;
						font-size: 16px;
						font-weight: 700;
					}
					.el-icon-arrow-down {
						font-size: 12px;
					}
				}
			}

			.tags-view {
				display: flex;
				justify-content: space-between;
				// align-items: center;
				box-sizing: border-box;
				position: absolute;
				width: 100%;
				height: $tags_height;
				padding: 0 20px;
				//border: 1px solid #f00;
			}

			.app-main {
				position: absolute;
				width: calc(100% - 80px);
				height: calc(100vh - 120px);
				border-radius: 10px;
				left: 50%;
				transform: translate(-50%, #{$tags_height});
				overflow: hidden;
				padding-bottom: 20px;
				background-color: rgba(255, 255, 255, 0.726);
				//border: 1px solid #f00;
			}
		}
	}

	.bottom-status {
		width: 100%;
		height: $status_height;
		line-height: $status_height;
		color: #fff;
		font-size: 12px;
		padding: 0 10px;
		background-color: rgb(71, 75, 117);
		position: absolute;
		bottom: 0;
		z-index: 100;
	}
}
</style>